.transactionHistory {
    width: 1000px;
    height: 400px;
    margin: 50px auto 0px auto;
    box-shadow: 0px 1px 1px rgba(241, 240, 240, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
        1px 4px 6px rgba(241, 240, 240, 0.16);
    border-radius: 5px;
    border: none;
    background-color: #ffffff;
    table-layout: fixed;
    border-collapse: collapse;
}

.title {
    height: 50px;
    text-align: center;
    background-color: #1990b8;
    color: #ffffff;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.03;
    letter-spacing: 0.03em;
    text-transform: uppercase;
}
td, th {
    border: 1px solid #cbcbcc;
}

tr:hover td {
    background-color: #65ff51;
    color: #0a0a0a;
}

.table {
    text-align: center;
    height: 50px;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.03;
    letter-spacing: 0.03em;
    color: #454545;
    cursor: pointer;
}
.table:nth-child(2n) {
    background: #c3e0ec;
}